<template>
    <div>
        <div class="super_container single_post_quote">
		    <div class="container">
			    <div class="row" style="display: block">
                    <div>
                        <p class="align-cntr color-white" style="background-color: #f23344; font-size: 24px;"></p>
                    </div>
                    <p style="text-align: center"><img src="images/success.png"  width="15%"></p>
                    <div class="product_detail-container col-lg-10 offset-lg-2">
                        <div class="blog"  style="padding-top: 20px">
                            <h4 style="color: #e40e21; text-align: center">Transaction Completed</h4>
                            <div style="margin-top: 30px">
