                                    <p style="text-align:center; word-break: break-all;"><span style="font-size: 16px; color: #e40e21;" >Source Address: </span><span> 0xASDNI130GJSKJEO003313NKHRLMDJDOWSNF93MD93</span></p>
                                    <p style="text-align:center;"><span style="font-size: 16px; color: #e40e21;" >You have 2 days to complete your payment.  (Due Date: August 12, 2018)</span></p>
