<script>
    export default {
        data() {
            return {
                btcVal: 0.1
            }
        },
        computed: {
            convertToRSV: function() {
                var final;
                final = parseFloat(this.btcVal / 0.0005);
                return final;
            }
        }
    }
</script>

<style scoped>

    td {
