        border: 1px solid #828282;
        border-radius: 4px;
        font-family: "Open Sans", "Segoe UI", Tahoma, sans-serif;
        font-weight: 400;
        background-color: #ffffff;
        -webkit-appearance: none !important;
        -moz-appearance: none;
        padding: 10px;
        width: 150px;
        cursor: pointer;
    }

    .cancel-btn:hover {
        opacity: 0.6;
    }

    select {
        color: #000 !important;
        font-size: 1em;
    }
</style>




