<template>
    <div>
        <div class="super_container single_post_quote">
		    <div class="container">
			    <div class="row" style="display: block">
                    <div>
                        <p class="align-cntr color-white" style="background-color: #f23344; font-size: 24px;">Buy RSV</p>
                    </div>
                    <div class="product_detail-container col-lg-10 offset-lg-2">
                        <div class="blog"  style="padding-top: 20px">
                            <!-- <div  class="form-group">
                                <div>
                                    <p>Budget for puchase</p>
                                    <input id="rsv-val" type="number" min="0.1" step="0.1" v-model="btcVal" class="form-control col-4 display_ib" >
                                    <select class="display_ib fa fa-sort-down">
                                        <option class="fa fa-sort-down">BTC</option>
                                        <option>ETH</option>
                                    </select>
                                </div>
                                <div style="padding-top:20px;">
                                    <div class="display_ib cart-info1">
                                        <p class="cart-info-title">Number of Tokens</p>
                                        <p>{{ convertToRSV }}</p>
                                    </div>
                                    <div class="display_ib cart-info1">
                                        <p class="cart-info-title">Bonus</p>
                                        <p>20%</p>
                                    </div>
                                    <div class="display_ib cart-info1">
                                        <p class="cart-info-title">Rate per Token</p>
                                        <p>0.0005 BTC</p>
                                    </div>
                                </div>
                            </div> -->
                            <div class="quantity">
                                <label style="display: inline-block;">Number of RSV Tokens: </label>
                                <input class="form-control col-2" id="btc-val" style=" text-align:right; display: inline-block;" type="number" v-model="convertToBTC">
                                <p style="display: inline-block;"> x </p>
                                <label>
                                    <select>
                                        <option>BTC</option>
                                        <option>ETH</option>
                                    </select>
                                </label>
                                <label>0.0005</label>
                                <p style="display: inline-block;"> = </p>
                                 <input class="form-control col-2" id="rsv-val" style=" text-align:right; display: inline-block;" type="number" min="0.1" step="0.1" v-model="btcVal">BTC
                                
                            </div>
                            <div>
                                <p class="display_ib">Bonus (20%): </p>
                                <p class="display_ib">{{ bonus }}</p>
                            </div>
                            <div>
                                <p class="display_ib">Total Tokens: </p>
                                <p class="display_ib">{{ total }}</p>
                            </div>
                            <div>
                                <p class="display_ib">Total Payment: </p>
                                <p class="display_ib">{{ btcVal }} BTC</p>
                            </div>
                            <div>
                                <p class="display_ib">Source Address: </p>
                                <select class="display_ib">
                                    <option>0xASDNI130GJSKJEO003313NKHRLMDJDOWSNF93MD93</option>
                                </select>
                            </div>
                            <div style="padding-top:30px; text-align:center;">
                                <p style="font-size: 0.8em">By clicking "Buy" you accept the <a href="#">Token Purchase Agreement</a></p>
                                <router-link to="/productdetail1"><button class="cancel-btn">Cancel</button></router-link>
                                <router-link to="/checkout"><button class="pdetail-button pd-sell" data-toggle="modal" data-target="#checkoutAlert">BUY</button></router-link>
                            </div>
                        </div>
                    </div>
			    </div>
		    </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                btcVal: 0.1,
                convertToBTC: '',
                bonus: '',
                total: ''
            }
        },
        computed: {
            convertToRSV: function() {
                var final;
                final = parseFloat(this.btcVal / 0.0005);
                this.convertToBTC = final;
                this.bonus = this.convertToBTC * .20;
                this.total = this.convertToBTC + this.bonus;
            }
        }
    }
</script>

<style scoped>
    .display_ib {
        display: inline-block;
    }

    .mrg_0a {
        margin: 0 auto;
    }

    .align-cntr {
        text-align: center;
    }

    .color-white {
        color: #ffffff;
    }

    .cart-info1 {
        background-color: #feeced;
        padding: 20px;
        width: 30%;
        margin: 10px;
    }

    .cart-info1 p {
        /* color: #f1484a !important; */
        font-size: 1.000em;
        text-align: center;
    }

    .cart-info-title {
        color: #f1484a !important;
        font-weight: 500;
        font-size: 1.125em !important;
    }

    .cancel-btn {
        color: #828282;
        border: 1px solid #828282;
        border-radius: 4px;
        font-family: "Open Sans", "Segoe UI", Tahoma, sans-serif;
        font-weight: 400;
        background-color: #ffffff;
        -webkit-appearance: none !important;
        -moz-appearance: none;
        padding: 10px;
        width: 150px;
        cursor: pointer;
    }

    .cancel-btn:hover {
        opacity: 0.6;
    }

    select {
        color: #000 !important;
        font-size: 1em;
    }
</style>




