<template>
    <div>
        <div class="super_container single_post_quote">
		    <div class="container">
			    <div class="row">
                    <div class="product_detail-container col-lg-10 offset-lg-2">
                        <div class="blog"  style="padding-top: 20px">
                            <div class="detail-page-logo"><img src="images/rsv-title-img.png" width="100%"></div>
                            
                            <div class="pdetail-button-div ">
                                
                                <router-link to="/cart"><button class="pdetail-button pd-buy">ADD TO CART</button></router-link>
                                <!-- <button class="pdetail-button pd-sell">BUY</button> -->
                            </div>
                            <p>Real Estate & Service Apartment & Vacation Rental Token</p>
                            <div class="presale-detail">
                                <div class="presale-txt"><span class="ps-txt1">Presale starts Today!</span></div>
                                <div class="progress" style="display: inline-block">
                                    <div class="progress-bar" role="progressbar" style="width: 25%;" aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">25%</div>
                                </div>
                            </div>
                            <div class="quantity">
                                <label style="display: inline-block;">Number of RSV Tokens: <p style="display: inline-block;">{{ convertToRSV }}</p></label>
                                <input class="form-control col-2" id="btc-val" style=" text-align:right; display: inline-block;" type="number" v-model="convertToBTC">
                                <p style="display: inline-block;"> x </p>
                                <label>
                                    <select>
                                        <option>BTC</option>
                                        <option>ETH</option>
                                    </select>
                                </label>
                                <label>0.0005</label>
                                <p style="display: inline-block;"> = </p>
                                 <input class="form-control col-2" id="rsv-val" style=" text-align:right; display: inline-block;" type="number" min="0.1" step="0.1" v-model="btcVal">BTC
                                
                            </div>
                            </div>
                        </div>
                    </div>
			    </div>
		    </div>
        </div>
    </div>
</template>


 <script>
export default {
    data() {
        return {
            btcVal: 0.1,
            convertToBTC: '',
            bonus: '',
            total: ''

        }
    },
    computed: {
        convertToRSV: function() {
            var final;
            final = this.btcVal / 0.0005;
            this.convertToBTC = final;
            this.bonus = this.convertToBTC * .20;
            this.total = this.convertToBTC + this.bonus;
        }
    }
}
</script>

<style scoped>
    hr {
        width: 30px;
        height: 3px;
        margin: 0;
        border: 0;
        box-sizing: none;
        background-color: #6e1c0e;
    }

    .tkn-div2 {
        border-bottom: 1px solid #d8d8d8;
        margin-bottom: 20px;
    }

    .tkn-div2 p {
        margin-bottom: 2px !important;
    }

    .tkn-sale {
        color: #000000 !important;
    }

    .single_post_text p:not(:first-of-type) {
        margin-top: 0 !important;
    }

    .bg-maroon {
        background: #6e1c0e;
    }

    .wd-40 {
        width: 40%;
        width: 0.400em;
    }

    .color-yllw1 {
        color: #fec006;
    }

    .color-white {
        color: #ffffff;
    }

    .align-cntr {
        text-align: center;
    }

    .margin-all-10 {
        margin: 10px;
    }

    .padding-all-10 {
        padding: 10px;
    }

    .font-size-20 {
        font-size: 20px;
        font-size: 1.250em;
    }

    .font-size-16 {
        font-size: 16px;
        font-size: 1.000em;
    }

    table.one2-1 th {
        background: #f5484a;
        color: #FFF;
        padding: 5px 15px;
        border-bottom: #FFF solid 1px;
        vertical-align: top;
    }

    table.one2-1 td {
        background: #FFF;
        padding: 5px 15px;
        border-left: #f5484a solid 1px;
        border-right: #f5484a solid 1px;
        border-bottom: #f5484a solid 1px;
        border-top: #f5484a solid 1px;
        vertical-align: top;
    }

    table.one2-1 {
        border-collapse: collapse;
        width: 100%;
        margin-bottom: 0px;
    }

    .box-text {
        /* padding: 0.25em 0.5em; */
        padding-left: 10px;
        font-size: 1.125em;
        background: transparent;
        border-left: solid 10px #f5484a;
    }

    .box-text p{
        /* padding: 0.25em 0.5em; */
        font-size: 1.125em;
        font-weight: 500;
    }

    .pd-tp20 {
        padding-top: 20px;
    }

    .pd-btm20 {
        padding-bottom: 20px;
    }
</style>





