import Vue from 'vue';
import Router from 'vue-router';
import Home from '../components/Home/Home.vue';
import Register from '../components/Register/Register.vue';
import ProductList from '../components/Product/ProductList.vue';
import ProductDetail1 from '../components/Product/ProductDetail1.vue';
import Cart from '../components/Cart/Cart.vue';
import Checkout from '../components/Cart/Checkout.vue';
import Completion from '../components/Cart/Completion.vue';
import Profile from '../components/Register/Profile.vue';
import Verify from '../components/Register/Verify.vue';
import Kyc from '../components/Register/Kyc.vue';
import Complete from '../components/Register/Complete.vue';

Vue.use(Router);

export default new Router({
  routes: [
    { path: '/', component: Home, name: 'Home' },
    { path: '/register', component: Register, name: 'Register' },
    { path: '/productlist', component: ProductList, name: 'ProductList' },
    { path: '/productdetail1', component: ProductDetail1, name: 'ProductDetail1' },
    { path: '/cart', component: Cart, name: 'Cart' },
    { path: '/checkout', component: Checkout, name: 'Checkout' },
    { path: '/completion', component: Completion, name: 'Completion' },
    { path: '/profile', component: Profile, name: 'Profile' },
    { path: '/verify', component: Verify, name: 'Verify' },
    { path: '/kyc', component: Kyc, name: 'Kyc' },
    { path: '/complete', component: Complete, name: 'Complete' }
  ]
})



